<template>
  <div class="new-left-side">
    <div class="left-side-nav">
      <router-link to="/Home">
        <div class="nav-img">
          <img src="@/assets/imgs/nav-1.png" alt="" class="img-nav" />
        </div>
        <p class="nav-title">HOME</p>
      </router-link>
    </div>
    <div class="left-side-nav" @click="toAboutus">
      <a>
        <div class="nav-img">
          <img src="@/assets/imgs/nav-2.png" alt="" class="img-nav" />
        </div>
        <p class="nav-title">ABOUT US</p>
      </a>
    </div>
    <div class="left-side-nav">
      <router-link to="/Citys">
        <div class="nav-img">
          <img src="@/assets/imgs/nav-3.png" alt="" class="img-nav" />
        </div>
        <p class="nav-title">CITIES IN UTOPIA</p>
      </router-link>
    </div>
    <div class="left-side-nav">
      <a href="https://meta-utopia.gitbook.io/" target="_blank">
        <div class="nav-img">
          <img src="@/assets/imgs/nav-4.png" alt="" class="img-nav" />
        </div>
        <p class="nav-title">GITBOOK</p>
      </a>
    </div>
    <div class="left-side-nav">
      <router-link to="/OutRoadmap">
        <div class="nav-img">
          <img src="@/assets/imgs/nav-5.png" alt="" class="img-nav" />
        </div>
        <p class="nav-title">OUR ROADMAP</p>
      </router-link>
    </div>
    <div class="left-side-nav">
      <router-link to="/Faq">
        <div class="nav-img">
          <img src="@/assets/imgs/nav-6.png" alt="" class="img-nav" />
        </div>
        <p class="nav-title">FAQ</p>
      </router-link>
    </div>
  </div>
</template>

<script>
export default {
  name: "Menus",
  data() {
    return {};
  },
  methods: {
    toAboutus() {
      console.log("luyou", this.$route);
      if (this.$route.name != "Home") {
        this.$router.push({
          path: "/Home",
          query: {
            tag: "aboutus",
          },
        });
      } else {
        this.$emit("handlePage");
      }
    },
  },
};
</script>


