<template>
  <div class="content-footer">
    <div class="footer-left">
      <div class="footer-left-item">
        <p class="footer-item-title">GET iN TOUCH WITH US</p>
        <p class="footer-item-desc">GET IN TOUCH WITH US</p>
      </div>
      <div class="footer-left-item">
        <p class="footer-item-title">FOLLOW US</p>
        <p class="footer-item-desc">We'd love to hear your thoughts.</p>
      </div>
    </div>
    <div class="footer-middle">
      <div class="footer-middle-item">
        <router-link to="/Home"><p class="item-nav">Home</p></router-link>
        <a @click="toAboutus"><p class="item-nav">About Us</p></a>
        <router-link to="/Citys"><p class="item-nav">Cities in UTOPIA</p></router-link>
      </div>
      <div class="footer-middle-item">
        <p class="item-nav"><a href="https://meta-utopia.gitbook.io/" target="_blank">GITBOOK</a></p>
        <router-link to="/OutRoadmap"><p class="item-nav">Our Roadmap</p></router-link>
        <router-link to="/Faq"><p class="item-nav">FAQ</p></router-link>
      </div>
    </div>
    <div class="footer-right">
      <div class="footer-logo">
        <img
          src="@/assets/imgs/footer-logo.png"
          class="img-footer-logo"
          alt=""
        />
        <p class="footer-logo-follow">FOLLOW US</p>
      </div>

      <div class="footer-icon">
        <div class="footer-icon-item">
          <a href="https://t.me/MW_MetaUtopia" target="_blank">
            <img src="@/assets/imgs/header-icon1.png" alt="" class="img-icons" />
          </a>
          
        </div>
        <div class="footer-icon-item">
          <a href="https://twitter.com/mw_metautopia" target="_blank">
            <img src="@/assets/imgs/header-icon2.png" alt="" class="img-icons" />
          </a>
        </div>
        <div class="footer-icon-item">
          <a href="https://discord.gg/HK2CRqhPfQ" target="_blank">
            <img src="@/assets/imgs/header-icon3.png" alt="" class="img-icons" />
          </a>
        </div>
        <div class="footer-icon-item">
          <a href="https://tttttt.me/Nic_MetaUtopiacs" target="_blank">
            <img src="@/assets/imgs/header-icon5.png" alt="" class="img-icons" />
          </a>
        </div>
        <div class="footer-icon-item">
          <a href="#" target="_blank">
            <img src="@/assets/imgs/header-icon4.png" alt="" class="img-icons" />
          </a>
          
        </div>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  name: "Footer",
  data() {
    return {
    };
  },
  methods:{
    toAboutus(){
      console.log('luyou',this.$route);
      if(this.$route.name != 'Home'){
        this.$router.push({
            path: '/Home',
            query:{
              tag:'aboutus'
            }
        });
      }else{
        this.$emit('handlePage');
      }
      
    },
    scrollTop(){
      if(this.$route.name != 'OutRoadmap'){
        this.$router.push({
            path: '/OutRoadmap'
        });
      }else{
        this.$emit('handleTop');
      }
              
    }
  },
};
</script>


