<template>
  <div class="left-side">
      <div style="height:2px;"></div>
  </div>
</template>

<script>
export default {
  name: "Menu",
  data() {
    return {};
  },
  methods: {
    toAboutus() {
      console.log("luyou", this.$route);
      if (this.$route.name != "Home") {
        this.$router.push({
          path: "/Home",
          query: {
            tag: "aboutus",
          },
        });
      } else {
        this.$emit("handlePage");
      }
    },
  },
};
</script>


